import Vue from 'vue'
import Vuex from 'vuex'
import vueResource from 'vue-resource'
Vue.use(Vuex);
Vue.use(vueResource);
//读取本地微信openid 有的话直接跳转   没有的话 不管
var wxopenid=localStorage.getItem("openId");
var wxopenid="oWk-fuP7uqLUJcoX118Mt6oWyFZo";

Vue.http.get("http://120.24.6.91:8082/wx/powderSystem/virUserByOpenId?openid="+wxopenid).then((response) =>{
	console.log(response.body);
	//如果为空 跳转登录页面
	if(typeof(response.body)==undefined || typeof(response.body)==null){
		console.log("跳转登录");
	}
	//如果有 保存
});

export default new Vuex.Store({
	state: {
		// 使用vuex保存当前用户身份 1 为普通用户   2 为 管理员
		uid: 2,
		// 聊天对象
		practiceObj:{
			// 消息数组对象
			selfSendPracticeList:[
				// {
				// 	userid: 179, 
				// 	username: "刘小龙", 
				// 	useravator: "./face.png", 
				// 	type: 1, 
				// 	content: "到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点", 
				// 	contentId: 941,
				// 	timer: 1495790331046, 
				// 	ismy: true, 
				// 	dataType: 1
				// }
			],
			// 签到
			signIn: false
		},
		// 保存当前请求地址
		url: 'http://120.24.6.91:8082'
	},
	mutations: {
		setUid (state, val) {
			state.uid = val;
		}
	}
}) 




